* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    padding: 20px;
    position: relative; /* Position the body relative */
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    position: relative; /* Position the container relative */
}

h1 {
    text-align: center;
    margin-bottom: 20px;
}

.photo-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
}

.photo-item {
    position: relative; /* Make the container for the image and title relative */
    border-radius: 5px;
    overflow: hidden;
}

.photo-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.photo-title {
    position: absolute; /* Position the title absolutely */
    bottom: 10px; /* Adjust the positioning as desired */
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4); /* Add a semi-transparent background */
    padding: 10px;
    text-align: center;
    border-bottom-left-radius: 5px; /* Ensure rounded corners */
    border-bottom-right-radius: 5px;
    color: white; /* Text color */
}

.background-overlay {
    position: absolute; /* Position the background overlay absolutely */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4); /* Background overlay with 40% opacity */
    z-index: -1; /* Place the background behind other content */
}
